import { Component, OnInit } from '@angular/core';
import { AppService } from '../../apps/shared/app.service';
import { LoadingController, ToastController, ActionSheetController, AlertController } from '@ionic/angular';
import { ActivatedRoute, Router } from '@angular/router';

import { ProfileService } from '../../person/shared/profile.service';
import { PersonService } from '../../person/shared/person.service';

@Component({
  selector: 'app-manage',
  templateUrl: './manage.page.html',
  styleUrls: ['./manage.page.scss'],
})
export class ManagePage implements OnInit {
  app: any = {};
  profiles: any;
  people: any;

  constructor(
    public appService: AppService,
    public profileService: ProfileService,
    public personService: PersonService,
    public loadingController: LoadingController,
    public route: ActivatedRoute,
    public router: Router,
    public toastController: ToastController,
    public actionSheetController: ActionSheetController,
    public alertController: AlertController
  ) { }

  ngOnInit() {
    this.getApp();
  }

  async getApp() {
    const loading = await this.loadingController.create({
      message: 'Loading'
    });
    await loading.present();
    await this.appService.getById(+this.route.snapshot.paramMap.get('id'))
      .subscribe(res => {
        console.log(res);
        this.app = res;
        loading.dismiss();
      }, err => {
        console.log(err);
        loading.dismiss();
        this.presentToast('Erro ao consultar. Tente novamente!');
      });
  }

  async getProfiles() {
    const loading = await this.loadingController.create({
      message: 'Loading'
    });
    await loading.present();
    await this.profileService.getAll()
      .subscribe(res => {
        console.log(res);
        this.profiles = res;
        loading.dismiss();
      }, err => {
        console.log(err);
        loading.dismiss();
        this.presentToast('Erro ao consultar. Tente novamente!');
      });
  }

  async presentToast(message: string) {
    const toast = await this.toastController.create({
      message: message,
      duration: 2000,
      showCloseButton: true,
      closeButtonText: 'Fechar'
    });
    toast.present();
  }

  async getPeopleByProfile(profileId: number) {
    const loading = await this.loadingController.create({
      message: 'Loading'
    });
    await loading.present();
    await this.profileService.getAll()
      .subscribe(res => {
        console.log(res);
        this.profiles = res;
        loading.dismiss();
      }, err => {
        console.log(err);
        loading.dismiss();
        this.presentToast('Erro ao consultar. Tente novamente!');
      });
  }
}
